<template>
    <div v-if="!showRaw" class="tag-panel" v-html="htmlContent"></div>
    <div v-else-if="showRaw" class="tag-panel">{{ htmlContent }}</div>
</template>





<script>

export default {

  name: 'TagPanel',

  props: ['htmlContent', 'showRaw'],

  beforeUpdate(){
      this.$emit("beforePanelRender");
  },

  updated(){ 
      this.$nextTick(
          function(){
            this.$emit("panelRendered");             
          }
      );

  },

  mounted(){
      this.$nextTick(
          function(){
            this.$emit("panelRendered");            
          }
      );

  }
}
</script>






<style lang="scss">
  @import 'scss/_colours';


.tag-panel {
    font-size:0.8125em;
        line-height:1.3;
        background-color: rgba(255,255,255,0.7);

    ul {
        padding-left:0;
    }
}

.sd-panel{
    border: $sd-thin-border;
    box-sizing: border-box;
    padding: 0.5em;
    border-radius: $sd-border-radius;
    border-color: $sd-blue;
    position:relative;
    margin: 1em 0 2em;
    list-style-type:none;
    color:#45679b;
    // background-color:$sd-pale-bk;
    box-shadow: 2px 2px 2px 0 #b7c0ce;
}

.sd-panel:first-child{
    margin-top:2em;
}

.sd-panel:before{
    content:"Panel";
    display:block;
    position:absolute;
    font-size:11px;
    left:8px;
    top:-15px;
    color:#fff;
    background-color:$sd-blue;
    padding:0 4px;
    border-top-left-radius:6px;
    border-top-right-radius:6px;
    z-index:0;
    box-shadow: 1px 0px 1px 0 #b7c0ce;


}

.sd-tag:after {
    font-size:1.7em;
    position:relative;
    top:0.15em;
    margin-left:3px;

  }

  .sd-tag {
    display:inline-block;
    line-height:1.7em;
    padding:2px;
    border-radius:4px;
    padding:4px;
    padding-top:2px;
    background-color:darken($sd-pale-bk,10%);
    color:darken($sd-pale-bk, 60%);
    }

  .sd-tag.intervention {
    color: $sd-red;
    background-color: lighten( $sd-red, 30%);
  }

  .sd-tag.assayed {
    color:darken($sd-blue, 20%);
    background-color: lighten( $sd-blue, 30%);
  }

    .sd-tag.reporter {
    color:darken($sd-green, 20%);
    background-color: lighten( $sd-green, 30%);
  }

  .type_score_0 { opacity:0.5; }
  .type_score_1 { opacity:0.6; }
  .type_score_2 { opacity:0.65; }
  .type_score_3 { opacity:0.7; }
  .type_score_4 { opacity:0.75; }
  .type_score_5 { opacity:0.8; }
  .type_score_6 { opacity:0.85; }
  .type_score_7 { opacity:0.9; }
  .type_score_8 { opacity:0.95; }
  .type_score_9 { opacity:1; }

 
</style>



