<template>
    <div class="cyto-chart" id="cytoscape">
   
    </div>

</template>





<script>
var cyto = require('cytoscape');

export default {

  name: 'CytoChart',

  props: ['chartJson'],

  data(){

    return {

        chart: null,
        c_container: null,
        config: {

                    style: [ // the stylesheet for the graph
                        {
                        selector: 'node',
                        style: {
                            'background-color': '#717171',
                            'label': 'data(term)',
                            'border-width':'4',
                            'border-style':'solid',
                            'border-color':'#717171',
                            'color':'#717171',
                            'font-size':12,

                        }
                        },

                        {
                            selector: 'edge',
                            style: {
                                'curve-style':'bezier',
                                'width': 3,
                                'line-color': '#94bf3d',
                                'target-arrow-color': '#94bf3d',
                                'target-arrow-shape': 'triangle',
                                'label':'may influence',
                                'edge-text-rotation':'autorotate',
                                "text-outline-color": "#94bf3d",
                                'color':'#ffffff',
                            "text-outline-width": 3,
                            "font-size":10,
                            'opacity':0.9
                            }
                        },
                        {
                            selector: 'node.intervention',
                            style: {
                                'background-color':'#cb5b3c',
                                'border-color':'#cb5b3c',
                                'color':'#cb5b3c'
                            }
                        },
                        {
                            selector: 'node.assayed',
                            style: {
                                'background-color':'#2f5f9d',
                                'border-color':'#2f5f9d',
                                'color':'#2f5f9d'
                            }
                        },
                        {
                            selector: 'node.reporter',
                            style: {
                                'background-color':'#587224',
                                'border-color':'#587224',
                                'color':'#587224'
                            }
                        },
                        {
                            selector: 'node.gene',
                            style: {
                                'background-image':'src/assets/gene.svg',
                                'background-fit':'cover',
                            }
                        },
                        {
                            selector: 'node.protein',
                            style: {
                                'background-image':'src/assets/protein.svg',
                                'background-fit':'cover',
                            }
                        },
                        {
                            selector: 'node.geneprod',
                            style: {
                                'background-image':'src/assets/geneprod.svg',
                                'background-fit':'cover',
                            }
                        },
                        {
                            selector: 'node.molecule',
                            style: {
                                'background-image':'src/assets/molecule.svg',
                                'background-fit':'cover',
                            }
                        },
                        {
                            selector: 'node.tissue',
                            style: {
                                'background-image':'src/assets/tissue.svg',
                                'background-fit':'cover',
                            }
                        },
                        {
                            selector: 'node.subcellular',
                            style: {
                                'background-image':'src/assets/subcellular.svg',
                                'background-fit':'cover',
                            }
                        },
                        {
                            selector: 'node.organism',
                            style: {
                                'background-image':'src/assets/organism.svg',
                                'background-fit':'cover',
                            }
                        },
                        {
                            selector: 'node.cell',
                            style: {
                                'background-image':'src/assets/cell.svg',
                                'background-fit':'cover',
                            }
                        },
                        {
                            selector: 'node.assay',
                            style: {
                                'background-image':'src/assets/assay.svg',
                                'background-fit':'cover',
                            }
                        },



                    ],

                    layout: {
                        name: 'grid'
                    }
        }
        
    }

  }, /* end of data */

    mounted(){
        this.c_container = document.getElementById('cytoscape');
        this.refreshChart();
    
    },

    watch:{

        chartJson(){
            this.refreshChart();
        }

    },
    methods:{
        refreshChart(){
                 // console.log(c_container.parentElement.clientHeight);
                if(this.chart){
                    this.chart.destroy();
                    this.chart = null;
                }

                let c_container = this.c_container;
                //fix for stupid Safari that doesn't calculate the height of the container as 100% of parent height
                if(!(c_container.clientHeight||c_container.style.height) && c_container.parentElement){
                    c_container.style.height = c_container.parentElement.clientHeight + "px";
                }
                

                this.chart = cyto({
                    container: c_container, 
                    elements: this.chartJson,
                    layout: {name: 'grid', componentSpacing:15, idealEdgeLength: 45},
                });

                this.chart.style(this.config.style);
                this.chart.resize();
                // this.chart.fit();
            
        }

    }

}
</script>






<style lang="scss">
  @import 'scss/_colours';

 #cytoscape {
     height:100%;
 }
</style>


