<template>

      <article>
        <section id="input" class="container">
        <ul id="Errors" v-if="restErrors.length > 0">
          <li v-for="error in restErrors">Error: {{error.message}}</li>
        </ul>
          <label for="sdTagInput">Enter a panel legend <span v-if="typing" id="typing">[Parsing will start as soon as you stop typing...]</span></label>
          <div class="textAreaWrapper">
            <textarea name="sdTagInput" id="sdTagInput" cols="30" rows="10" placeholder="Copy or type a panel legend" v-model="textInput" @input="dataEntry"></textarea>
            <div id="outputTypes">
              <strong>Output format</strong>
              <ul>
                <li><input v-model="showHtmlPanel" type="checkbox" name="taggedAndPanel" id="taggedAndPanel" checked disabled><label for="taggedAndPanel">Tagged HTML with panel segmentation</label></li>
                <li><input @change="showXmlToggle" v-model="showXml" type="checkbox" name="taggedXml" id="taggedXml"><label for="taggedXml">Tagged SourceData XML</label></li>
                <li><input @change="showSegmentsToggle" v-model="showPanelSegments" type="checkbox" name="panelSegment" id="panelSegment"><label for="panelSegment">Panel Segments</label></li>
                <li><input @change="showTaggingToggle" v-model="showTagging" type="checkbox" name="tagOnly" id="tagOnly"><label for="tagOnly">Tagging only</label></li>  
                <li><input @change="showRolesToggle" v-model="showRoles" type="checkbox" name="fetchRoles" id="fetchRoles"><label for="fetchRoles">Fetch roles for tagged entities</label></li>  
              </ul>

              <button @click="clearAll" class="clearButton">Clear</button>
            </div>
      
          </div>
          <div><a id="demoText" href="#" @click.prevent="demo">Try with example text</a> <a id="demoText2" href="#" @click.prevent="demo2">Have some fun!</a></div>
        </section>
        <section id="output"  v-bind:class="['container', resultHtml ? '' : 'faded']">
        <h2>Results</h2> <img v-if="showLoader" src="src/assets/loader.gif" alt="loading animation">
        <div id="tagPanelContainer">
          <div id="tagPanelText">
            <tag-panel v-if="resultHtml" :htmlContent="resultHtml" @panelRendered="renderChart" @beforePanelRender="deactivateChart"></tag-panel>
          </div>
          <div id="tagPanelChart">
              <cyto-chart v-if="showChart&&resultJson" :chartJson="resultJson" ></cyto-chart>
          </div>
        </div>
        



            <div id="otherPanels" >
            <transition name="fade">            <div v-if="showXml&&resultXml" id="xmlDisplay"><tag-panel :htmlContent="resultXml"  @panelRendered="deactivateLoader"></tag-panel></div></transition>
            <transition name="fade">            <div v-if="showPanelSegments&&resultSegments" id="segmentsDisplay"><tag-panel :htmlContent="resultSegments" @panelRendered="deactivateLoader"></tag-panel></div></transition>
            <transition name="fade"><div v-if="showTagging&&resultTags" id="tagsDisplay"><tag-panel :htmlContent="resultTags" @panelRendered="deactivateLoader"></tag-panel></div></transition>
            <transition name="fade"><div v-if="showRoles&&resultRoles" id="rolesDisplay"><tag-panel :htmlContent="resultRoles" @panelRendered="deactivateLoader"></tag-panel></div></transition>


            
            
            </div>
        
            <aside id="iconKey" class="sd-icons">
              <div>
                <figure>
                  <span class="cell"></span>
                  <figcaption>Cell</figcaption>
                </figure>
              </div>
              <div>
                <figure>
                  <span class="subcellular"></span>
                  <figcaption>Subcellular Structure</figcaption>
                </figure>
              </div>
              <div>
                <figure>
                  <span class="geneprod"></span>
                  <figcaption>Gene Product</figcaption>
                </figure>
              </div>
              <div>
                <figure>
                  <span class="organism"></span>
                  <figcaption>Organism</figcaption>
                </figure>
              </div>
              <div>
                <figure>
                  <span class="tissue"></span>
                  <figcaption>Tissue</figcaption>
                </figure>
              </div>
              <div>
                <figure>
                  <span class="molecule"></span>
                  <figcaption>Molecule</figcaption>
                </figure>
              </div>
              <div>
                <figure>
                  <span class="assay"></span>
                  <figcaption>Assay Method</figcaption>
                </figure>
              </div>

            </aside>
            <aside id="key-blocks">
                <span class="intervention key-block">Intervention</span>
                <span class="assayed key-block">Assayed</span>
                <span class="reporter key-block">Reporter</span>
            </aside>
        
        </section>
      </article>


</template>

<script>
import debounce from 'debounce'
import axios from 'axios'
import qs from 'qs'
import TagPanel from './TagPanel.vue'
import CytoChart from './CytoChart.vue'
import xmlParse from 'xml-js'


export default {

  name: 'app',
  components: {
    TagPanel,
    CytoChart,

  },

  data () {
    return {
      typing: false,
      textInput:"",
      showLoader: false,
      showHtmlPanel:true,
      showXml: false,
      showPanelSegments: false,
      showTagging: false,
      showRoles: false,
      showChart:false,
      resultHtml:null,
      resultJson:null,
      resultXml:null,
      resultSegments:null,
      resultTags:null,
      tagsXml:null,
      resultRoles:null,
      restErrors:[],
      demoText: `(a) Macrophages were treated with control (Ctrl), Drp1, or Mfn1 siRNA for 72 hr in presence of absence of FK506. Mitochondria and autophagosome lysates were probed as in (b) by immunoblot for Drp1 and Mfn1 to show the efficiency of depletion. (b) We also observed a reduced expression of Bcl-2 expression levels in the brain, heart and kidney of Drp1-deficient mice. (c) Calcium levels are significantly reduced.`,
      demoText2: `Trump-1 overexpression in the brain of Melania typhimurium provoked the collapse of the America-293 cell population and a decrease of optimismolol secretion as assessed by kremlinoblotting.`,
      baseUrl:'http://sdtag.net:7475/',
      restEndpoints:{
        'all':{endpoint:'smtag', format:'html'},
        'xml':{endpoint:'smtag', format:'xml'},
        'segments':{endpoint:'panelize', format:'html'},
        'tags':{endpoint:'entity', format:'html'},
        'tagsxml':{endpoint:'entity', format:'xml'},
        'roles':{endpoint:'role', format:''}
        // 'json':{endpoint:'chart', format:'json'},
      }
    }
  },

  computed: {

    showAdditionalResults(){

      //check if the checkboxes for the additional results types are checked and there is 
      // data to show
      if((this.showXml && this.resultXml)||(this.showTagging && this.resultTags)||(this.resultSegments && this.showPanelSegments)){
        return true;
      }else{
        return false;
      }

    },

  },

   methods: {

    demo(){

      // this.process();
      this.textInput = this.demoText;
      this.process();

    },
    demo2(){

      // this.process();
      this.textInput = this.demoText2;
      this.process();

    },
    dataEntry(){
      this.typing = true;
      this.process();
    },
    clearAll(){
      this.textInput = '';
      this.resultHtml = null;
      this.resultJson = null;
      this.resultXml = null;
      this.resultSegments = null;
      this.resultTags = null;
      this.tagsXml = null;      
      this.resultRoles = null;      
    },
    process: debounce(function(){

      this.restErrors = [];


      if(this.textInput){
        this.showLoader = true;
        let that = this;

        // get the basic data
        // note the use of promise syntax! This is an asynchronous request that returns a
        // promise that must be dealt with using "then" for success and "catch" for failure
        this.fetch('all')
        .then(function(response){

          that.resultHtml = response.data;

        }).catch(function(error){
          this.restErrors.push(error);
        });

        //get the XML to use in XML display and to convert into charting JSON
        this.fetch('xml')
        .then(function(response){
        
          //parse the XML result into chart JSON
          if(response.data) { that.parseXmlToJson(response.data); }

          //update XML display if the XML checkbox is checked
          if(that.showXml){ that.resultXml = that.escapeAndFormatXml(response.data);
          that.deactivateLoader();
           }

        }).catch(function(error){
              that.restErrors.push(error);
        });

        //get the Panels
        if(this.showPanelSegments){

          this.fetch('segments')
            .then(function(response){


              that.resultSegments = response.data;
              that.deactivateLoader();

            }).catch(function(error){
              this.restErrors.push(error);
            });

        }//end of fetch Panels

        //get the Tags
        if(this.showTagging){

          this.fetch('tags')
            .then(function(response){


              that.resultTags = response.data;
              that.deactivateLoader();
            }).catch(function(error){
              this.restErrors.push(error);
            });

        }//end of fetch Tags

        //get the Roles from XML
        if(this.showRoles){

          this.fetch('tagsxml')
            .then(function(response){


              that.tagsXml = response.data;

              that.fetch('roles')
                .then(function(response){
                  
                  that.resultRoles = that.escapeAndFormatXml(response.data);
                                    
                }).catch(function(error){
                  that.restErrors.push(error);
                });


              that.deactivateLoader();
            }).catch(function(error){
              that.restErrors.push(error);
            });

        }//end of fetch Tags




      }else{
        //if input is empty
        this.resultHtml = null;
        this.resultXml = null;
        this.resultSegments = null;
        this.resultTags = null;
        this.resultJson = null;
        this.resultRoles = null;
      }

      this.typing = false;



    },500),

    // perform the AJAX request to the funky AI tag generating thing
    fetch(requestType){

      var that = this;
      
      //special method for Roles request
      if(requestType=='roles'){
        var theData = qs.stringify({pretagged: that.tagsXml/*.replace(/\//g," / ")*/});
      }
      else
      {
        var theData = qs.stringify({format:that.restEndpoints[requestType].format, text: that.textInput/*.replace(/\//g," / ")*/});
      }

      // Axios here returns a promise to the calling function
      // dealing with the outcome of the promise is the calling function's job!
      // console.log(requestType);
      // console.log(that.restEndpoints[requestType]);
      return axios
        .post(
          that.baseUrl + that.restEndpoints[requestType].endpoint, 
          theData,
          {
            headers: {
              'Content-Type': "application/x-www-form-urlencoded; charset=UTF-8",
            }
          }
        );


    },// end of fetch
    deactivateChart(){
      // console.log("preparing panel");
      this.showChart = false;

    },
    renderChart(){

      // console.log("panel rendered");
      this.showChart = true;
      this.deactivateLoader();

    },
    deactivateLoader(){
      this.showLoader = false;
    },
    escapeAndFormatXml(xmlData){

      return xmlData
         .replace(/&/g, "&amp;")
         .replace(/</g, "&lt;")
         .replace(/>/g, "&gt;")
         .replace(/"/g, "&quot;")
         .replace(/'/g, "&#039;")
         .replace(/&amp;#47;/g, "/")
         .replace(/(&lt;sd-panel&gt;)/g,"<div style='color:#4a4a4a; font-style:italic;'>$1</div><div style='padding-left:15px;'>")
         .replace(/(&lt;smtag&gt;)/g,"<span style='color:#4a4a4a; font-style:italic;'>$1</span><div style='padding-left:15px;'>")
         .replace(/(&lt;\/sd-panel&gt;)/g,"</div><div style='color:#4a4a4a; font-style:italic;'>$1</div>")
         .replace(/(&lt;\/smtag&gt;)/g,"</div><span style='color:#4a4a4a; font-style:italic;'>$1</span>")
         .replace(/(&lt;sd-tag.*?&gt;)/g,"<span style='color:#4a4a4a; font-style:italic;'>$1</span>")
         .replace(/(&lt;\/sd-tag&gt;)/g,"<span style='color:#4a4a4a; font-style:italic;'>$1</span>");

    },

    parseXmlToJson(xmlData){

      //add a root node for valid XML
    
      let theXml = xmlData;
     
      //convert XML into Json
      let theJson = JSON.parse(xmlParse.xml2json( theXml, {compact: true, spaces: 4}));
      // console.log(theJson);
      //to store the formatted charting json
      let chartJson = {};
      chartJson.nodes = [],
      chartJson.edges = [];

      let nonArraySdTagCount = 0;


      let thePanels = theJson.smtag["sd-panel"];


      if(thePanels && ! Array.isArray(thePanels) ) {
        thePanels = [thePanels];
      }


      //loop through each panel
      for(let i=0; i<thePanels.length; i++){

        //loop through tags
        if(! thePanels[i]["sd-tag"] ) {
          continue;
        }
        else if( ! Array.isArray(thePanels[i]["sd-tag"]) ){

          //use category if it exists
          let theType = (thePanels[i]["sd-tag"]._attributes.category) ? thePanels[i]["sd-tag"]._attributes.category : thePanels[i]["sd-tag"]._attributes.type;

          //check strength of prediction
          let theStrength = parseFloat((thePanels[i]["sd-tag"]._attributes.category_score) ? thePanels[i]["sd-tag"]._attributes.category_score : thePanels[i]["sd-tag"]._attributes.type_score) / 100;

          chartJson['nodes'].push(
            {data:{
              id: i.toString() + "_" + nonArraySdTagCount.toString(),
              term: thePanels[i]["sd-tag"]._text,
              type: theType,
              role: thePanels[i]["sd-tag"]._attributes.role,
              panelOrigin: i.toString(),
              opacity: theStrength
              },
              classes: theType + ' ' + thePanels[i]["sd-tag"]._attributes.role
            }
          )

          nonArraySdTagCount++;

        }else{


          for (let j=0; j<thePanels[i]["sd-tag"].length; j++){

            //use category if it exists
            let theType = (thePanels[i]["sd-tag"][j]._attributes.category) ? thePanels[i]["sd-tag"][j]._attributes.category : thePanels[i]["sd-tag"][j]._attributes.type;

            //check strength of prediction
            let theStrength = parseFloat((thePanels[i]["sd-tag"][j]._attributes.category_score) ? thePanels[i]["sd-tag"][j]._attributes.category_score : thePanels[i]["sd-tag"][j]._attributes.type_score) / 100;

            chartJson['nodes'].push(
              {data:{
                id: i.toString() + j.toString(),
                term: thePanels[i]["sd-tag"][j]._text,
                type: theType,
                role: thePanels[i]["sd-tag"][j]._attributes.role,
                panelOrigin: i.toString(),
                opacity: theStrength
                },
                classes: theType + ' ' + thePanels[i]["sd-tag"][j]._attributes.role
              }
            )
          }
        }//end of check for object or array on sd-tags




      }

      //OK, now to generate the Edges!
      for(let k=0;k<chartJson.nodes.length;k++){

        if( chartJson.nodes[k].data.role == "intervention" ){

          for(let m=0;m<chartJson.nodes.length;m++){
            
            if( (chartJson.nodes[m].data.panelOrigin == chartJson.nodes[k].data.panelOrigin) &&  chartJson.nodes[m].data.role == "assayed" ){
              chartJson.edges.push({data: { source: chartJson.nodes[k].data.id, target: chartJson.nodes[m].data.id }})
            }

          }

        }

      }


      this.resultJson = chartJson;

    },

    showXmlToggle(){
      if(this.showXml){
        this.process();
      }
    },
    showSegmentsToggle(){
      if(this.showPanelSegments){
        this.process();
      }
    },
    showTaggingToggle(){
      if(this.showTagging){
        this.process();
      }
    },
    showRolesToggle(){
      if(this.showRoles){
        this.process();
      }
    },



  }

}
</script>




<style lang="scss">
  @import 'scss/_colours';

  .faded {
    opacity:0.3;
  }

  .fade-enter-active, .fade-leave-active {
  transition: opacity 0.6s
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0
}

  #output {
    transition: opacity 0.6s ease-in;
  }

  #output h2 {
    display:inline-block;
  }

  #output img {
    vertical-align:middle;
    margin-left:15px;
  }

  #Errors {
    font-size:small;
    background-color:#ffe0ea;
    color:#ff355c;
    padding:1em;
    border-radius:$sd-border-radius;

    li {
      margin-left:15px;
    }
  }

  #input {
    label, textarea {
        display:block;
        box-sizing:border-box;
    }

    textarea{
        border:none;
        background-color:#fff;
        width:60%;
        max-width:60%;
        margin:0.5em;
        color:#45679b;
        font-size:0.8125em;
        line-height:1.5;
        
    }

    textarea:hover,
    textarea:focus,
    textarea:active{
        outline:none;
        border:none;
    }

    label {
        margin-left:0.5em;
        font-size:small;
        color:$sd-blue;
    }

    .textAreaWrapper {
        display:flex;
        border:$sd-thick-border;
        border-radius:$sd-border-radius;
        border-color:$sd-blue;
        background-color: #fff;
        box-shadow: 1px 1px 1px 0 #b7c0ce;        
    }

    #outputTypes {
        flex-grow:1;
        position:relative;
        background-color: $sd-pale-bk;
        padding:0.5em;
        padding-left:1em;
        margin:0;

        strong {
            display:inline-block;
            font-size:small;
            padding-bottom:0.5em;
        }

        ul {
            list-style-type: none;
            margin:0;
            padding:0;


        }

        li {
            display:block;
            padding-bottom:0.5em;

            input, label {
                display:inline-block;
            }
        }

        .clearButton {
          font-size:small;
          color:darken($sd-pale-bk, 60%);
          background-color:white;
          position:absolute;
          bottom:5px;
          left:1em;
          border: solid 1px darken($sd-pale-bk, 40%);
          border-radius:4px;
          cursor:pointer;
          transition: all 0.25s linear;
        }

        .clearButton:focus,
        .clearButton:hover
        {
          outline:none;
          background-color:lighten($sd-blue,10%);
          color:white;
        }
        .clearButton:active{
          outline:none;
          background-color:$sd-blue;
          color:white;
        }
    }

    #demoText, #demoText2 {
        font-size:0.8125em;
        padding-left:0.5em;
    }

}

  



  #tagPanelContainer {
    border: $sd-thick-border;
    border-color:$sd-blue;
    border-radius: $sd-border-radius;
    box-sizing:border-box;
    display:flex;
    flex-wrap:wrap;
    box-shadow: 1px 1px 1px 0 #b7c0ce;

    &>div {
      width:50%;
      flex-grow:1;
      align-items:stretch;
    }
  }

  #tagPanelChart, cyto-chart {
    min-height:150px;
   
  }

  #tagPanelChart {
    box-sizing:border-box;
    padding:10px;
  }

  #iconKey {
    display:flex;
    text-align:center;
    color: darken($sd-pale-bk, 60%);
    background-color:$sd-pale-bk;
    border: solid 1px darken($sd-pale-bk, 10%);
    margin-top:1em;


    div {
      flex-grow:1;
      flex-basis:0;
  

    }


    span {
      font-size:2em;
    }

    figcaption{
      font-size:small;
    }
  }

  #key-blocks {
    display:block;
    margin-top:0.6em;
    font-size:small;

  .key-block {
    border-radius: 8px;
    margin-right:8px;
    padding:4px 8px;
  }
  
  .reporter {
    color:darken($sd-green, 20%);
    background-color:lighten( $sd-green, 30%);
  }
  .assayed {
    color:darken($sd-blue, 20%);
    background-color:lighten( $sd-blue, 30%);
  } 
  .intervention {
    color:darken($sd-red, 20%);
    background-color:lighten( $sd-red, 30%);
  } 
  
  }



  #tagPanelText > .tag-panel {
    padding-left:1em;
  }

  #otherPanels {
    display:flex;
    flex-wrap:wrap;
    margin-top:3em;

    &>div {
      position:relative;
      transition: opacity 0.2s ease-in;
      flex-grow:1;
      flex-basis:0;
      padding:0.6em;
      margin-right:0.6em;
      border:$sd-thick-border;
      border-radius:$sd-border-radius;
      border-color:$sd-blue;
      color:#45679b;
      box-shadow: 1px 1px 1px 0 #b7c0ce;

    }

    &>div:last-child {
      margin-right:0;
    }

    #xmlDisplay:before,
    #segmentsDisplay:before,
    #tagsDisplay:before,
    #rolesDisplay:before {
      content:"";
      display:block;
      font-size:0.8125em;
      color:$sd-blue;
      position:absolute;
      left:1em;
      top:-1.5em;

    }    

    #xmlDisplay:before{ content:"Tagged XML"; }
    #segmentsDisplay:before{ content:"Panel segmentation"; } 
    #tagsDisplay:before{content:"Tagging only"; }
    #rolesDisplay:before{content:"Roles for pre-tagged XML"; }

  }     

  #typing {
    font-size:small;
    color:#ff355c;
    font-style:italic;
  }

</style>



